import styled from 'styled-components'

const ControlBoard = styled.div`
  background: linear-gradient(90deg, rgba(124,122,121,1) 0%, rgba(142,142,142,1) 20%, rgba(166,165,165,1) 50%, rgba(142,142,142,1) 80%, rgba(124,122,121,1) 100%);
  height: 66%;
  border: solid;
  display: flex;
`
export default ControlBoard



