import styled from 'styled-components'

const LogoTitle = styled.p`
  text-shadow: 1px 1px 2px purple, 0 0 1em blue, 0 0 0.2em blue;
  margin: 0;
  font-size: ${props => props.size}%;
  font-family: 'Kanit', sans-serif;
`
export default LogoTitle



